@import url("../../node_modules/highlight.js/styles/monokai.css");
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    table{
        @apply w-full border-collapse border border-gray-300;
    }
    th,td{
        @apply border border-gray-300 p-2;
    }
    a{
        @apply !text-blue-700 ;
    }
    tr{
        @apply odd:bg-white even:bg-slate-50;
    }
    thead tr{
        @apply !bg-slate-200 !text-slate-900;
    }
}
